import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);
export default new Router({
  mode: "hash",
  routes: [
    {
      path: "/",
      redirect: "/index"
    },
    {
      path: "/login",
      component: () => import("@/pages/login/login.vue"),
      hidden: true,
      name: "登录",
      meta: { title: "登录", noCache: true, affix: true }
    },
    {
      path: "/layout",
      component: () => import("@/pages/layout/index.vue"),
      children: [
        {
          path: "/index",
          component: () => import("@/pages/index/index.vue"),
          hidden: true,
          name: "首页",
          meta: { title: "首页", noCache: true, affix: true }
        },
        {
          path: "/classic",
          component: () => import("@/pages/classic/classic.vue"),
          name: "经典",
          meta: { title: "经典", noCache: true, affix: true, breadNumber: 1 }
        },
        {
          path: "/classic/details",
          component: () => import("@/pages/classic/details.vue"),
          name: "经典详情",
          meta: {
            title: "经典详情",
            noCache: true,
            affix: true,
            breadNumber: 2
          }
        },
        {
          path: "/classic/classicmore",
          component: () => import("@/pages/classic/components/classicmore.vue"),
          name: "经典全部",
          meta: { title: "经典全部", noCache: true, affix: true }
        },
        {
          path: "/video",
          component: () => import("@/pages/video/video.vue"),
          name: "视频",
          meta: { title: "视频", noCache: true, affix: true }
        },
        {
          path: "/video/details",
          component: () => import("@/pages/video/details.vue"),
          name: "视频详情",
          meta: { title: "视频详情", noCache: true, affix: true }
        },
        {
          path: "/video/videomore",
          component: () => import("@/pages/video/components/videomore.vue"),
          name: "视频全部",
          meta: { title: "视频全部", noCache: true, affix: true }
        },
        {
          path: "/q&Answer",
          component: () => import("@/pages/q&Answer/Q&Answer.vue"),
          name: "问答",
          meta: { title: "问答", noCache: true, affix: true }
        },
        {
          path: "/activity",
          component: () => import("@/pages/activity/activity.vue"),
          name: "活动",
          meta: { title: "活动", noCache: true, affix: true }
        },
        {
          path: "/activity/details",
          component: () => import("@/pages/activity/details.vue"),
          name: "活动详情",
          meta: { title: "活动详情", noCache: true, affix: true }
        },
        {
          path: "/product",
          component: () => import("@/pages/product/product.vue"),
          name: "产品",
          meta: { title: "产品", noCache: true, affix: true }
        },
        {
          path: "/searchResult",
          component: () => import("@/pages/searchResult/searchResult.vue"),
          name: "搜索",
          meta: { title: "搜索", noCache: true, affix: true }
        },
        {
          path: "/vlog",
          component: () => import("@/pages/vlog/vlog.vue"),
          name: "课程",
          meta: { title: "课程", noCache: true, affix: true }
        },
        {
          path: "/vlog/vlogmore",
          component: () => import("@/pages/vlog/components/vlogmore.vue"),
          name: "课程全部",
          meta: { title: "课程全部", noCache: true, affix: true }
        },
        {
          path: "/engineering",
          component: () => import("@/pages/engineering/engineering.vue"),
          name: "工程",
          meta: { title: "工程", noCache: true, affix: true }
        },
        {
          path: "/personal",
          component: () => import("@/pages/personal/personal.vue"),
          name: "个人中心",
          meta: { title: "个人中心", noCache: true, affix: true },
          children: [
            {
              path: "/personal/message",
              component: () =>
                import("./pages/personal/components/message.vue"),
              name: "/personal/messages",
              meta: { title: "消息中心", noCache: true, affix: true }
            },
            {
              path: "/personal/collection",
              component: () =>
                import("./pages/personal/components/collection.vue"),
              name: "/personal/collection",
              meta: { title: "收藏", noCache: true, affix: true }
            },
            {
              path: "/personal/printing",
              component: () =>
                import("./pages/personal/components/printing.vue"),
              name: "/personal/printing",
              meta: { title: "倡印", noCache: true, affix: true }
            },
            {
              path: "/personal/wechat",
              component: () => import("./pages/personal/components/wechat.vue"),
              name: "/personal/wechat",
              meta: { title: "微信", noCache: true, affix: true }
            },
            {
              path: "/personal/iphone",
              component: () => import("./pages/personal/components/iphone.vue"),
              name: "/personal/iphone",
              meta: { title: "手机", noCache: true, affix: true }
            },
            {
              path: "/personal/datasete",
              component: () =>
                import("./pages/personal/components/datasete.vue"),
              name: "/personal/datasete",
              meta: { title: "个人资料", noCache: true, affix: true }
            },
            {
              path: "/personal/address",
              component: () =>
                import("./pages/personal/components/address.vue"),
              name: "/personal/address",
              meta: { title: "收货地址", noCache: true, affix: true }
            }
          ]
        }
      ]
    }
  ]
});
