import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import axios from "axios";
import store from "./store";
import "./plugins/element.js";
import "@/assets/css/base.css";
import "@/assets/css/icon.css";
import "@/assets/css/named_color.scss";
import ElementUI from "element-ui";
// import '../node_modules/element-ui/lib/theme-chalk/index.css'
import VideoPlayer from "vue-video-player";
import {
  provinceAndCityData,
  regionData,
  provinceAndCityDataPlus,
  regionDataPlus,
  CodeToText,
  TextToCode
} from "element-china-area-data";
require("video.js/dist/video-js.css");
require("vue-video-player/src/custom-theme.css");
axios.defaults.withCredentials = true;
Vue.prototype.$axios = axios;
Vue.use(VideoPlayer);
Vue.use(ElementUI);
Vue.use(
  provinceAndCityData,
  regionData,
  provinceAndCityDataPlus,
  regionDataPlus,
  CodeToText,
  TextToCode
);
Vue.config.productionTip = false;

const isTest = false;
var APIURL = "";
if (process.env.NODE_ENV === "development") {
  APIURL = "";
} else {
  if (isTest) {
    APIURL = "https://upload.hanyujingyun.cn/dock";
  } else {
    APIURL = "https://dock.lihan.ltd/dock";
  }
}
router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  const defaultTitle = "李罕诵经网";
  if (to.meta.title) {
    document.title = to.meta.title
      ? to.meta.title + "_" + "李罕诵经"
      : defaultTitle;
  }
  if (to.meta.title == "首页") {
    document.title = defaultTitle;
  }
  next();
});
Vue.prototype.APIURL = APIURL;
Vue.prototype.defaultUserImg =
  "https://mhd.hanyujingyun.cn/images/lihansongjing.svg";
Vue.prototype.defaultPcBackImg =
  "https://mhb.hanyujingyun.cn/image%2FdefaultPcBackImg.jpg";

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
