import Vue from "vue";
import Vuex from "vuex";
import CreatePersistedState from "vuex-persistedstate";
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    classicId: "",
    chapterId: "",
    bookDetails: "",
    bookList: "",
    videoList: "",
    audioList: "",
    video: "",
    classicsList: "",
    seen: false,
    flag: false,
    UserInfo: "",
    QadidlogShow: false,
    searchResult: "",
    SessionKey: "",
    key: "",
    routePath: "",
    breadList: [],
    collectionClassic: ""
  },
  mutations: {
    setFlag(state, value) {
      state.flag = value;
      // console.log(value)
    },
    // 经典收藏集合
    setCollectionClassic(state, value) {
      state.collectionClassic = value;
    },
    // 面包屑集合
    setBreadList(state, value) {
      state.breadListe = value;
      console.log(39, value);
    },
    // sessionKey
    setSessionKey: function(state, value) {
      state.SessionKey = value;
      // console.log(value)
    },
    setKey: function(state, value) {
      state.key = value;
    },
    // 搜索集合
    setSearchResult: function(state, value) {
      state.searchResult = value;
      // console.log(value)
    },
    setQadidlogShow: function(state, value) {
      state.QadidlogShow = value;
    },
    // 用户信息
    setUserInfo: function(state, value) {
      // console.log(value)
      state.UserInfo = value;
    },
    // 底部播放器显示控制
    setSeen: function(state, val) {
      state.seen = val;
    },
    setHideBar: function(state, val) {
      state.isHideBar = val;
    },
    setClassicId: function(state, value) {
      state.classicId = value;
      console.log(value);
    },
    setChapterId: function(state, value) {
      state.chapterId = value;
    },
    // setBookDetails: function (state, value) {
    //   console.log(value)
    //   state.bookDetails = value
    // },
    setBookList: function(state, value) {
      state.bookList = value;
      // console.log(value)
    },
    setAudioList: function(state, value) {
      state.audioList = value;
      console.log(value);
    },
    setVideo: function(state, value) {
      state.video = value;
      // console.log(value)
    },
    setVideoList: function(state, value) {
      state.videoList = value;
      // console.log(value)
    },
    setClassicsList: function(state, value) {
      state.classicsList = value;
    }
  },
  getters: {
    breadListState() {
      return JSON.parse(sessionStorage.getItem("breadListStorage")) || [];
    }
  },
  // vuex 数据持久化插件,不需要手动保存在sessionStorage或者localstorage
  plugins: [
    CreatePersistedState({
      storage: window.sessionStorage,
      reducer(state) {
        return {
          // 只储存state中的需要持久保存的数据
          videoList: state.videoList,
          video: state.video,
          classicsList: state.classicsList,
          SessionKey: state.SessionKey,
          key: state.key,
          bookList: state.bookList,
          collectionClassic: state.collectionClassic,
          breadList: state.breadList
        };
      }
    })
  ]
});
